import {
  appendHeader,
  assignAdminNavbar,
  correctNavbarHref,
  fetchUrl,
  formatDuration,
  formatTimestampToAMPM,
  getDateFromTimestamp,
  getToken,
  logoutBtnOnClick,
  redirectTo,
  reloadDelay,
  removeOptions,
} from "../../util";
import { ROLES } from "../../util/constants";
import { closeModal, openModal } from "../../util/modal";
import { initSearch } from "../../util/search";
import {
  XANO_ADMIN_SESSIONS_SEARCH_URL,
  XANO_ADMIN_SESSIONS_URL,
  XANO_ADMIN_SIGN_IN_REDIRECT,
  XANO_ADMIN_NEW_RECURRING_SESSION_URL,
  XANO_ADMIN_NEW_SESSION_URL,
  XANO_ADMIN_DELETE_RECURRING_SESSION_URL,
} from "../../util/xano_urls";

let UrlGymId;
let addManualSessionFormBlock,
  selectGymEl,
  selectSessionNameEl,
  selectCoachEl,
  selectDateEl,
  selectTimeEl,
  selectDurationEl,
  selectCapacityEl,
  selectAddSessionBtnEl;

window.onload = async function () {
  logoutBtnOnClick("/admin/sign-in", "sidebar-logout-btn");

  var curUrl = new URL(document.location.href);
  UrlGymId = curUrl.searchParams.get("gymCode") || null;

  if (UrlGymId) correctNavbarHref(UrlGymId);
  else {
    // ? TODO: Change this to a modal showcasing the error
    console.error("No gym id found");
    return;
  }

 const token = getToken();
  if (token) {
    const result = await fetchUrl(XANO_ADMIN_SESSIONS_URL + UrlGymId, "GET", false, token);

    if (result.isFetchOk !== undefined && !result.isFetchOk) {
      console.error(result.message);
      // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
      return;
    }

    const { team_role, ...data } = result;

    if (team_role === ROLES.SUPER_ADMIN || team_role === ROLES.ADMIN) {
      assignAdminNavbar(team_role);
      getSuperAdminData(data, token);
    } else {
      redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=UNAUTHORIZED_USER`);
    }
  } else {
    redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
  }
};

$(document).ready(function () {
  addManualSessionFormBlock = document.getElementById("add-manual-session-form-block");
  selectGymEl = addManualSessionFormBlock.querySelector("#sessions-select-gym");
  selectSessionNameEl = addManualSessionFormBlock.querySelector("#session-name-input");
  selectCoachEl = addManualSessionFormBlock.querySelector("#sessions-select-coach");
  selectDateEl = addManualSessionFormBlock.querySelector("#datepicker-input");
  selectTimeEl = addManualSessionFormBlock.querySelector("#timepicker-input");
  selectDurationEl = addManualSessionFormBlock.querySelector("#sessions-select-duration");
  selectCapacityEl = addManualSessionFormBlock.querySelector("#session-select-capacity");
  selectAddSessionBtnEl = addManualSessionFormBlock.querySelector("#sessions-select-btn");

  selectGymEl.disabled = true;
  selectSessionNameEl.disabled = true;
  selectCoachEl.disabled = true;
  selectDateEl.disabled = true;
  selectTimeEl.disabled = true;
  selectDurationEl.disabled = true;
  selectCapacityEl.disabled = true;
  selectAddSessionBtnEl.disabled = true;

  $(selectDateEl).datepicker({
    startDate: new Date(),
    format: "dd/mm/yyyy",
    language: "en",
    multidate: true,
    orientation: "bottom auto",
  });

  $(selectTimeEl).timepicker({
    timeFormat: "h:mm p",
    interval: 15,
    defaultTime: "7",
    minTime: "7",
    maxTime: "11:45pm",
  });
});

async function getSuperAdminData(data, token) {
  const { manual_type_sessions, recurring_type_sessions, recurring_sessions, gym } = data;

  selectGymEl.disabled = false;

  if (gym) appendHeader(gym);
  appendSessions([...manual_type_sessions, ...recurring_type_sessions], gym.code);
  appendRecursiveSessions(recurring_sessions, token);
  initAddManualSession(token);
  initSearch(
    {
      data: manual_type_sessions,
      token,
      API: XANO_ADMIN_SESSIONS_SEARCH_URL,
      extraXanoInputObj: [{ gym_id: UrlGymId }],
    },
    appendSessions,
    gym.code
  );
}

function appendSessions(data, code) {
  const tableContainer = document.getElementById("table-container");
  const og_webflow_element = tableContainer.querySelector("#table-item");

  function appendSessionsFn(_data) {
    let allItems = tableContainer.querySelectorAll(".adb-user.w-inline-block");
    allItems.forEach((item, indx) => {
      if (indx !== allItems.length - 1) item.remove();
    });

    _data.forEach((session, indx) => {
      const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
      clonedCard.setAttribute("id", "");
      clonedCard.setAttribute("href", `/admin/session?sessionId=${session.id}`);
      let el = clonedCard.querySelectorAll(".colcontent");

      if (indx % 2 === 0) clonedCard.style.backgroundColor = "#00edf4";

      el[1].innerText = session.session_name;
      el[2].innerText = session.name || "To Be Decided"; // ? this is coach name
      el[3].innerText = code;
      el[4].innerText = getDateFromTimestamp(session.time);
      el[5].innerText = formatTimestampToAMPM(session.time, true);
      el[6].innerText = session?.users?.length ?? 0;
      el[7].innerText = session.status;

      clonedCard.style.display = "flex";
      tableContainer.prepend(clonedCard);
    });
  }

  appendSessionsFn(data);
  og_webflow_element.style.display = "none";
}
function appendRecursiveSessions(data, token) {
  const tableContainer = document.getElementById("recursive-table-container");
  const tableColumns = tableContainer.querySelectorAll(".recursive-table-columns");

  function appendRecursiveSessionsFn(_data) {
    let RECURSIVE_DATA;
    tableColumns.forEach((colEl, i) => {
      let indx = i + 1;
      const tableWrap = colEl.querySelector("#recursive-table-wrapper");
      const og_webflow_element = tableWrap.querySelector("#recursive-table-item");

      _data.forEach((session) => {
        // ? session.weekday is a string
        if (session.weekday == indx) {
          const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
          clonedCard.setAttribute("id", "");
          let el = clonedCard.querySelectorAll(".colcontent");

          el[0].innerText = session.session_name;
          el[1].innerText = formatTimestampToAMPM(session.time, true);
          el[2].innerText = formatDuration(session.duration);
          el[3].innerText = session.capacity;

          const deleteRecurringSessionBtnContainer = clonedCard.querySelector(".delete-icon-w.off");
          clonedCard.onmouseover = () => {
            deleteRecurringSessionBtnContainer.style.display = "flex";
          };
          clonedCard.onmouseout = () => {
            deleteRecurringSessionBtnContainer.style.display = "none";
          };

          const deleteRecurringSessionBtn = clonedCard.querySelector(".delete-recurring-session");
          deleteRecurringSessionBtn.onclick = async () => {
            const disableAllRecurringSessionsBtn = document.querySelectorAll(".delete-recurring-session");
            disableAllRecurringSessionsBtn.forEach((btn) => {
              btn.disabled = true;
            });

            const xano_input = {
              recurring_id: session.id,
            };

            let result = await fetchUrl(XANO_ADMIN_DELETE_RECURRING_SESSION_URL, "POST", xano_input, token);

            if (result.isFetchOk !== undefined && !result.isFetchOk) {
              console.error(result.message);
              $("#add-recurring-session-form-block")
                .hide() // optional hiding of form
                .siblings(".w-form-done")
                .hide() // Hide success
                .siblings(".w-form-fail")
                .show() // Show failure
                .text("Failed to delete recurring session");

                reloadDelay(1500)
              return;
            }

            clonedCard.remove();

            $("#add-recurring-session-form-block")
              .hide() // optional hiding of form
              .siblings(".w-form-fail")
              .hide() // Hide failure
              .siblings(".w-form-done")
              .show() // Show success
              .text("Successfully deleted recurring session");

              reloadDelay(1500)
          };

          clonedCard.style.display = "flex";
          tableWrap.prepend(clonedCard);
        }
      });

      openModal(
        {
          _modalWindow: "recursive-popout",
          TriggerBtn: "add-recursive-weekday-btn",
          baseElement: colEl,
        },
        () => {
          const recursivePopout = document.getElementById("recursive-popout");
          const recursiveSessionNameEl = recursivePopout.querySelector("#recursive-sessions-name-select");
          const recursiveDurationEl = recursivePopout.querySelector("#recursive-sessions-duration-select");
          const recursiveCapacityEl = recursivePopout.querySelector("#recursive-sessions-capacity-select");
          const datepickerEl = recursivePopout.querySelector("#datepicker-input");
          const timepickerEl = recursivePopout.querySelector("#timepicker-input");
          const selectedWeekday = recursivePopout.querySelector("#selected-day");

          const weekdays = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"];
          removeOptions(datepickerEl);
          [weekdays[indx - 1]].forEach((weekday) => {
            var opt = document.createElement("option");
            opt.value = indx;
            opt.innerText = weekday;
            opt.selected = true;
            datepickerEl.appendChild(opt);
          });
          datepickerEl.disabled = true;

          selectedWeekday.innerText = weekdays[indx - 1];

          timepickerEl.style.color = "#080808";
          $(timepickerEl).timepicker({
            timeFormat: "HH:mm",
            interval: 15,
            defaultTime: "7",
            minTime: "7",
            maxTime: "11:45pm",
            zindex: 10,
          });

          removeOptions(recursiveDurationEl, 0);
          let durationName = [
            "15 mins",
            "30 mins",
            "45 mins",
            "1 hour",
            "1 hour and 15 mins",
            "1 hour and 30 mins",
            "1 hour and 45 mins",
            "2 hours",
            "2 hour and 15 mins",
            "2 hour and 30 mins",
            "2 hour and 45 mins",
            "3 hours",
            "3 hour and 15 mins",
            "3 hour and 30 mins",
            "3 hour and 45 mins",
          ];
          let durationId = [
            "0.25",
            "0.5",
            "0.75",
            "1",
            "1.25",
            "1.5",
            "1.75",
            "2",
            "2.25",
            "2.5",
            "2.75",
            "3",
            "3.25",
            "3.5",
            "3.75",
          ];
          durationName.forEach((name, indx) => {
            var opt = document.createElement("option");
            opt.value = durationId[indx];
            opt.innerText = name;
            recursiveDurationEl.appendChild(opt);
          });

          async function fetchAddSessionSelectData() {
            let data;
            if (RECURSIVE_DATA) {
              data = RECURSIVE_DATA;
            } else {
              data = await fetchUrl(XANO_ADMIN_NEW_SESSION_URL + UrlGymId, "GET", false, token);
            }

            if (data.isFetchOk !== undefined && !data.isFetchOk) {
              console.error(data.message);
              // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
              return;
            }

            RECURSIVE_DATA = data;

            const { gyms, session_names } = data;

            removeOptions(recursiveSessionNameEl);
            session_names.forEach((name) => {
              var opt = document.createElement("option");
              opt.value = name.name;
              opt.innerText = name.name;
              recursiveSessionNameEl.appendChild(opt);
            });

            recursiveCapacityEl.setAttribute("max", gyms[0].capacity);
            recursiveCapacityEl.setAttribute("placeholder", "Max capacity: " + gyms[0].capacity);

            recursiveSessionNameEl.removeEventListener("focus", fetchAddSessionSelectData);
          }
          // ? when the user clicks on the select dropdown, it will get data
          recursiveSessionNameEl.addEventListener("focus", fetchAddSessionSelectData);
        }
      );

      og_webflow_element.style.display = "none";
    });
  }
  closeModal({ _modalWindow: "recursive-popout", TriggerBtn: "close-recursive-session-popout-btn" });
  appendRecursiveSessionsFn(data);
}

function initAddManualSession(token) {
  $("form :input").attr("autocomplete", "off");

  async function fetchAddSessionSelectData() {
    let data = await fetchUrl(XANO_ADMIN_NEW_SESSION_URL + UrlGymId, "GET", false, token);

    if (data.isFetchOk !== undefined && !data.isFetchOk) {
      console.error(data.message);
      // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
      return;
    }

    selectSessionNameEl.disabled = false;
    selectCoachEl.disabled = false;
    selectDateEl.disabled = false;
    selectTimeEl.disabled = false;
    selectDurationEl.disabled = false;
    selectCapacityEl.disabled = false;

    const { gyms, coaches } = data;

    removeOptions(selectGymEl);
    gyms.forEach((gym, indx) => {
      var opt = document.createElement("option");
      opt.value = gym.id;
      opt.innerText = gym.location;
      if (indx === 0) opt.selected = true;
      selectGymEl.appendChild(opt);
    });

    removeOptions(selectCoachEl, 0);
    coaches.forEach((coach) => {
      var opt = document.createElement("option");
      opt.value = coach.id;
      opt.innerText = coach.name;
      selectCoachEl.appendChild(opt);
    });

    removeOptions(selectDurationEl, 0);
    let durationName = [
      "15 mins",
      "30 mins",
      "45 mins",
      "1 hour",
      "1 hour and 15 mins",
      "1 hour and 30 mins",
      "1 hour and 45 mins",
      "2 hours",
      "2 hour and 15 mins",
      "2 hour and 30 mins",
      "2 hour and 45 mins",
      "3 hours",
      "3 hour and 15 mins",
      "3 hour and 30 mins",
      "3 hour and 45 mins",
    ];
    let durationId = [
      "0.25",
      "0.5",
      "0.75",
      "1",
      "1.25",
      "1.5",
      "1.75",
      "2",
      "2.25",
      "2.5",
      "2.75",
      "3",
      "3.25",
      "3.5",
      "3.75",
    ];
    durationName.forEach((name, indx) => {
      var opt = document.createElement("option");
      opt.value = durationId[indx];
      opt.innerText = name;
      selectDurationEl.appendChild(opt);
    });

    selectCapacityEl.setAttribute("max", gyms[0].capacity);
    selectCapacityEl.setAttribute("placeholder", "Max capacity: " + gyms[0].capacity);
    selectGymEl.addEventListener(
      "change",
      () => {
        gyms.some((gym) => {
          if (gym.id === parseInt(selectGymEl.value)) {
            selectCapacityEl.setAttribute("max", gym.capacity);
            selectCapacityEl.setAttribute("placeholder", "Max capacity: " + gym.capacity);
            return true;
          } else return false;
        });
      },
      false
    );

    selectGymEl.removeEventListener("focus", fetchAddSessionSelectData);
  }
  // ? when the user clicks on the select dropdown, it will get data
  selectGymEl.addEventListener("focus", fetchAddSessionSelectData);
}

var Webflow = Webflow || [];
Webflow.push(function () {
  // unbind webflow form handling (keep this if you only want to affect specific forms)
  $(document).off("submit");
  /* Any form on the page */

  $("form").on("change", function () {
    const $form = $(this); // The submitted form
    if ($form.attr("id") === "add-manual-session-form-block") {
      let gym_id = $form[0][0].value;
      let session_name = $form[0][1].value;
      // let team_id = $form[0][2].value;
      let date = $form[0][3].value; // ? timeampm is not here since form cant detect when its changing, button doesnt show if its last
      let duration = $form[0][5].value;
      let capacity = $form[0][6].value;

      if (
        gym_id !== "" &&
        session_name !== "" &&
        session_name !== "Session Name" &&
        date !== "" &&
        duration !== "" &&
        duration !== "Duration" &&
        capacity.trim() !== ""
      ) {
        $form[0][7].disabled = false;
        $form[0][7].classList.remove("disabled");
      }
    }
  });

  $("form").on("submit", async function (e) {
    e.preventDefault();

    const $form = $(this); // The submitted form
    if ($form.attr("id") === "add-manual-session-form-block") {
     const token = getToken();
      if (token) {
        let gym_id = $form[0][0].value;
        let session_name = $form[0][1].value;
        let team_id = $form[0][2].value;
        let date = $form[0][3].value;
        let timeampm = $form[0][4].value;
        let duration = $form[0][5].value;
        let capacity = $form[0][6].value;

        if (
          gym_id !== "" &&
          session_name !== "" &&
          session_name !== "Session Name" &&
          date !== "" &&
          timeampm !== "" &&
          duration !== "" &&
          duration !== "Duration" &&
          capacity.trim() !== ""
        ) {
          let xano_input = {};
          xano_input.items = [
            ...date.split(",").map((d) => {
              let df = d.split("/");
              return {
                gym_id,
                session_name,
                team_id: team_id === "Coach" ? null : team_id,
                time: new Date(`${df[2]}/${df[1]}/${df[0]} ${timeampm}`).getTime(),
                duration,
                capacity,
              };
            }),
          ];

          let newSessionData = await fetchUrl(XANO_ADMIN_NEW_SESSION_URL + gym_id, "POST", xano_input, token);

          if (newSessionData.isFetchOk !== undefined && !newSessionData.isFetchOk) {
            console.error(newSessionData.message);
            $form
              .siblings(".w-form-done")
              .hide() // Hide success
              .siblings(".w-form-fail")
              .show() // show failure
              .text("There was an error trying to add your session. Please refresh your browser and try again.");

            // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
            return;
          }

          $form
            .hide() // optional hiding of form
            .siblings(".w-form-done")
            .show() // Show success
            .siblings(".w-form-fail")
            .hide(); // Hide failure

          window.location.reload();
        } else {
          if (session_name === "Session Name") $form[0][1].style.borderColor = "red";
          else $form[0][1].style.borderColor = "black";

          if (date.trim() === "") $form[0][3].style.borderColor = "red";
          else $form[0][3].style.borderColor = "black";

          if (timeampm.trim() === "") $form[0][4].style.borderColor = "red";
          else $form[0][4].style.borderColor = "black";

          if (duration === "Duration") $form[0][5].style.borderColor = "red";
          else $form[0][5].style.borderColor = "black";

          if (capacity.trim() === "") $form[0][6].style.borderColor = "red";
          else $form[0][6].style.borderColor = "black";

          $form
            .siblings(".w-form-done")
            .hide() // Hide success
            .siblings(".w-form-fail")
            .show() // show failure
            .text("Please fill out all fields");
        }
      }
    } else if ($form.attr("id") === "add-recurring-session-modal-form-block") {
     const token = getToken();
      if (token) {
        const session_name = $form[0][0].value;
        const weekday = $form[0][1].value;
        const timeampm = $form[0][2].value;
        const duration = $form[0][3].value;
        const capacity = $form[0][4].value;

        if (session_name !== "" && weekday !== "" && timeampm !== "" && duration !== "" && capacity !== "") {
          let xano_input = {
            gym_id: UrlGymId,
            session_name,
            weekday: parseInt(weekday),
            time: new Date().setHours(timeampm.split(":")[0], timeampm.split(":")[1], 0, 0),
            duration,
            capacity,
          };

          let newSessionData = await fetchUrl(XANO_ADMIN_NEW_RECURRING_SESSION_URL, "POST", xano_input, token);

          if (newSessionData.isFetchOk !== undefined && !newSessionData.isFetchOk) {
            console.error(newSessionData.message);
            $form
              .siblings(".w-form-done")
              .hide() // Hide success
              .siblings(".w-form-fail")
              .show() // show failure
              .text("There was an error trying to add your session. Please refresh your browser and try again.");

            // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
            return;
          }

          $form
            .hide() // optional hiding of form
            .siblings(".w-form-done")
            .show() // Show success
            .siblings(".w-form-fail")
            .hide(); // Hide failure

          window.location.reload();
        }
      }
    }
  });
});
