import { fetchUrl } from ".";

/*
 * @param {String}   options.mode - search via api or from client data
 * CLIENT MODE:
 * @param {String}   options.clientObjRefName - The 'name' of the object reference to search through
 * @param {Array}    options.data - The data to search through
 *
 * API MODE:
 * @param {String}   options.API - The API URL to use to call and search
 * @param {String}   options.token - The token to use for authentication when making API calls
 * @param {Array}    options.extraXanoInputObj - Extra Xano input object to send with the API call (ex: gym_id, session_name, etc)
 *
 * @param {String}   options.searchContainerElId - The id of the search container element for focused control (incase you have many search inputs)
 * @param {Function} callback - The function to call after search
 * @param {Object}   secondParamData - A second parameter to pass to the callback function
 * @param {Function} errorHandlerFn - The function to call if there is an error
 */

export function initSearch(
  { mode = "API", clientObjRefName = "name", data, token, API, extraXanoInputObj = [], searchContainerElId = "" },
  callback,
  secondParamData = null,
  errorHandlerFn = null
) {
  let searchInput, searchBtn, resetBtn;

  // ? if you have multiple search inputs and you need to specify which one to use via parent container (searchContainerElId)
  if (searchContainerElId.length > 0) {
    let searchContainer = document.getElementById(searchContainerElId);
    searchInput = searchContainer.querySelector("#search-input");
    searchInput.setAttribute("placeholder", "Search");
    searchBtn = searchContainer.querySelector("#search-btn");
    resetBtn = searchContainer.querySelector("#search-reset-btn");
  } else {
    searchInput = document.getElementById("search-input");
    searchInput.setAttribute("placeholder", "Search");
    searchBtn = document.getElementById("search-btn");
    resetBtn = document.getElementById("search-reset-btn");
  }

  // ? will reset the search input and call the callback function with the original data
  resetBtn.onclick = () => {
    searchInput.value = "";
    searchData(
      resetBtn,
      { mode, clientObjRefName, data, searchquery: "", token, API, extraXanoInputObj },
      callback,
      secondParamData,
      errorHandlerFn
    );
  };

  // ? start search when user clicks on search button, either from client data or from API
  searchBtn.onclick = () => {
    let search = searchInput.value;

    searchData(
      resetBtn,
      { mode, clientObjRefName, data, searchquery: search, token, API, extraXanoInputObj },
      callback,
      secondParamData,
      errorHandlerFn
    );
  };
}

async function searchData(
  resetBtn,
  { mode, clientObjRefName, data, searchquery, token, API, extraXanoInputObj = [] },
  callback,
  secondParamData,
  errorHandlerFn
) {
  // ? if search query is empty, reset the search input and call the callback function with the original data
  if (searchquery.length === 0) {
    resetBtn.style.display = "none";
    if (secondParamData) callback(data, secondParamData);
    else callback(data);
  } else {
    try {
      if (mode === "API") {
        const xano_input = {
          search_query: `${searchquery}:*`,
        };

        // ? add extra xano inputs to the xano_input object
        if (extraXanoInputObj.length > 0) {
          extraXanoInputObj.map((obj) => {
            for (const [key, value] of Object.entries(obj)) {
              xano_input[key] = value;
            }
          });
        }

        // ? call the API
        let apiData = await fetchUrl(API + "query", "POST", xano_input, token);

        if (apiData.isFetchOk !== undefined && !apiData.isFetchOk) {
          console.error(apiData.message);
          // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
          throw Error("API call failed");
        }

        // ? if search was successful, show the reset button
        resetBtn.style.display = "flex";

        // ? call the callback function with the search data
        if (secondParamData) callback(apiData, secondParamData);
        else callback(apiData);
      } else {
        // ? if mode = client, search through the client data by specific object reference name
        let search_data = data.filter((d) => d[clientObjRefName].toLowerCase().includes(searchquery.toLowerCase()));

        // ? show the reset button
        resetBtn.style.display = "flex";

        // ? call the callback function with the search data
        if (secondParamData) callback(search_data, secondParamData);
        else callback(search_data);
      }
    } catch (e) {
      if (errorHandlerFn) errorHandlerFn();
      else console.error(e);
    }
  }
}
