/* Opening modal window function */
export function openModal(
  { single = true, baseElement = "", TriggerBtn = "jsModalOpen", _modalWindow = "jsModal" },
  callback
) {
  if (single) {
    var openModalBtn = baseElement.querySelector("#" + TriggerBtn);
    openModalBtn.onclick = function () {
      var modalWindow = document.getElementById(_modalWindow);

      if (callback) callback();

      modalWindow.classList ? modalWindow.classList.add("open") : (modalWindow.className += " " + "open");
    };
  } else {
    // ? TODO: If you are not using this else statement, just remove it ;p
    /* Get trigger element */
    var modalTrigger = document.getElementsByClassName(TriggerBtn);

    /* Set onclick event handler for all trigger elements */
    for (var i = 0; i < modalTrigger.length; i++) {
      modalTrigger[i].onclick = function () {
        var target = this.getAttribute("href").substr(1);
        var modalWindow = document.getElementById(target);

        modalWindow.classList ? modalWindow.classList.add("open") : (modalWindow.className += " " + "open");
      };
    }
  }
}

export function closeModal({ TriggerBtn = "jsModalClose", _modalWindow = "jsModal" }, callback) {
  /* Get close button */
  var closeButton = document.getElementById(TriggerBtn);
  var modalWindow = document.getElementById(_modalWindow);

  /* Set onclick event handler for close buttons */
  closeButton.onclick = function () {
    modalWindow.classList
      ? modalWindow.classList.remove("open")
      : (modalWindow.className = modalWindow.className.replace(
          new RegExp("(^|\\b)" + "open".split(" ").join("|") + "(\\b|$)", "gi"),
          " "
        ));
  };

  if (callback) callback();
}

export function openModalAuto({ _modalWindow = "jsModal" }, callback) {
  var modalWindow = document.getElementById(_modalWindow);

  function trigger() {
    modalWindow.classList ? modalWindow.classList.add("open") : (modalWindow.className += " " + "open");

    if (callback) callback();
  }

  trigger();
}

export function closeModalAuto({ _modalWindow }, callback) {
  var modalWindow = document.getElementById(_modalWindow);

  function trigger() {
    modalWindow.classList
      ? modalWindow.classList.remove("open")
      : (modalWindow.className = modalWindow.className.replace(
          new RegExp("(^|\\b)" + "open".split(" ").join("|") + "(\\b|$)", "gi"),
          " "
        ));

    if (callback) callback();
  }

  trigger();
}
